
import Header from "../components/header/Header.jsx";
import Footer from "../components/footer/Footer.jsx";
import React, { useState, useEffect } from 'react';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import eventsData from '../assets/events.json';


const ClubSchedule = () => {
  const [events, setEvents] = useState([]);

  useEffect(() => {
    const mappedEvents = eventsData.map(event => ({
      id: event.id,
      title: event.name,
      start: event.start_date,
      end: event.end_date,
    }));
    setEvents(mappedEvents);
  }, []);

  return (
    <div className="app-container">
      <Header />
      <div className="home-container">
        <div className="content-container">
          <div className="single-content-container">
            <h2>Club Schedule</h2>
            <FullCalendar
              plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
              initialView="timeGridWeek" // Starts with a week view that shows the time grid
              events={events} // Events loaded from your JSON file
              headerToolbar={{
                left: 'prev,next today',
                center: 'title',
                right: 'dayGridMonth,timeGridWeek'
              }}
              // editable={true}
              // selectable={true}
              slotMinTime="09:00:00" // Display calendar starting from 9 AM
              slotMaxTime="24:00:00" // Display calendar up until 12 AM (midnight)
            />
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default ClubSchedule;
